import React from 'react'
import imageList from './imageList.css';

const ImageList = (props)=> {
   const images = props.imageList.map(image=>{
         return <div className="images--image"> <img key={image.id} alt={image.description} src ={image.urls.regular} /></div>
     })
    return (
        <div className="images">
           
            {images}

          
        </div>
    )
}
export default ImageList